.header {
  height: 110px;
  padding-top: 23px;
  box-shadow: 0px 1px 14px 0px rgba(0, 0, 0, 0.12);
  background-color: #f3f7ef;

  &__inner {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}
.nav__list {
  display: flex;
  list-style: none;
}
.nav__item {
  margin-right: 50px;
}
.nav__item:last-child {
  margin-left: 23px;
}
.nav__link {
  color: #2f2a37;
  font-family: Tahoma, sans-serif;
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: 22px;
}
.button-lang {
  background-color: transparent;
  border: none;
  outline: none;
  position: relative;
  margin-right: 20px;
  cursor: pointer;
  &::after {
    content: "";
    position: absolute;
    right: -20px;
    top: 5px;
    background: url("../../img/svg/Vector.svg");
    height: 7px;
    width: 12px;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
  }
}
.nav__item--center {
  margin-left: 28px;
}
